
.nav-wrapper {
  position: fixed;
  top: 0;
  background-color: #fff;
  z-index: 100;
  width: 100%;
  height: 45px;
  overflow: hidden;
  white-space: nowrap;
  border-bottom: 1px solid #eee;
}
.nav-wrapper .nav-list {
  font-size: 0;
}
.nav-wrapper .nav-list .list {
  display: inline-block;
  width: 60px;
  border-sizing: border-box;
  margin-right: 4px;
  height: 43px;
  line-height: 43px;
  text-align: center;
}
.nav-wrapper .nav-list .list:first-child {
  margin-left: 14px;
}
.nav-wrapper .nav-list .list:last-child {
  margin-right: 0;
}
.nav-wrapper .nav-list .list-item {
  display: inline-block;
  width: 60px;
  font-size: 18px;
  color: #333;
}
.nav-wrapper .nav-list .list-item.active {
  color: #38f;
  border-bottom: 2px solid #38f;
}
.content-wrapper {
  border-top: 1px solid #eee;
}
.content-wrapper .content-list {
  margin: 0 12px;
}
.content-wrapper .content-list .list-wrapper {
  width: 100%;
  border-bottom: 1px solid #eee;
}
.content-wrapper .content-list .list-wrapper .list-item {
  font-size: 0;
}
.content-wrapper .content-list .list-wrapper .list-item .title {
  font-size: 18px;
  line-height: 26px;
  color: #333;
  margin: 8px 0 4px;
}
.content-wrapper .content-list .list-wrapper .list-item .big {
  display: block;
  position: relative;
}
.content-wrapper .content-list .list-wrapper .list-item .big .big-picture {
  vertical: top;
  width: 100%;
  height: auto;
}
.content-wrapper .content-list .list-wrapper .list-item .big .icon-16 {
  display: inline-block;
  width: 48px;
  height: 48px;
  font-size: 48px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -24px 0 0 -24px;
  color: #fff;
}
.content-wrapper .content-list .list-wrapper .list-item .user,
.content-wrapper .content-list .list-wrapper .list-item .time {
  display: inline-block;
  height: 38px;
  line-height: 38px;
  font-size: 13px;
  color: #999;
}
.content-wrapper .content-list .list-wrapper .list-item .user {
  margin-right: 8px;
}
.content-wrapper .content-list .list-wrapper .list-item .small-list .small {
  margin-right: 1px;
}
.content-wrapper .content-list .list-wrapper .list-item .small-list .small:last-child {
  margin-right: 0;
}
.content-wrapper .content-list .list-wrapper .list-item .small-list .small .small-picture {
  vertical-align: top;
  width: 33%;
  height: auto;
  display: inline-block;
}


