
.item {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 500;
  overflow: auto;
  background: #fff;
}
.item .wrap {
  min-height: 100%;
}
.item .wrap .return {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 44px;
  line-height: 44px;
  padding: 0 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #f8f8f8;
  border-bottom: 1px solid #d6d6d6;
  z-index: 20;
}
.item .wrap .return .icon-fanhui {
  float: left;
  font-size: 20px;
  color: #333;
}
.item .wrap .return .icon-dian {
  float: right;
  font-size: 20px;
  color: #333;
}
.item .wrap .content-wrap {
  padding-bottom: 64px;
}
.item .wrap .content-wrap .passage-wrap {
  padding: 10px 14px 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #f5f5f5;
  padding-top: 60px;
}
.item .wrap .content-wrap .passage-wrap .title {
  font-size: 22px;
  font-weight: bold;
  line-height: 1.3;
  color: #333;
}
.item .wrap .content-wrap .passage-wrap .author {
  font-size: 0;
  margin-top: 9px;
}
.item .wrap .content-wrap .passage-wrap .author .user,
.item .wrap .content-wrap .passage-wrap .author .time {
  font-size: 13px;
  color: #999;
  margin-right: 16px;
}
.item .wrap .content-wrap .passage-wrap .passage-pic .passage {
  margin: 20px 0 0;
  font-size: 20px;
  color: #333;
  line-height: 1.5;
}
.item .wrap .content-wrap .passage-wrap .passage-pic .pic {
  width: 100%;
  height: auto;
  margin-top: 20px;
}
.item .wrap .content-wrap .relative {
  background: #fff;
  padding: 0 16px;
}
.item .wrap .content-wrap .relative .top {
  position: relative;
  height: 44px;
  font-size: 16px;
  line-height: 44px;
  color: #999;
  border-bottom: 1px solid #e6e6e6;
}
.item .wrap .content-wrap .relative .top:after {
  position: absolute;
  content: "";
  height: 1px;
  width: 64px;
  background: #38f;
  left: 0;
  top: 100%;
}
.item .wrap .content-wrap .relative .bottom .recommend {
  height: 47px;
  line-height: 47px;
  font-size: 18px;
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-bottom: 1px solid #f0f0f0;
}
.item .wrap .content-wrap .video-wrapper {
  background: #f5f5f5;
  padding-bottom: 10px;
}
.item .wrap .content-wrap .video-wrapper .video {
  font-size: 0;
  position: relative;
}
.item .wrap .content-wrap .video-wrapper .video img {
  width: 100%;
  height: auto;
}
.item .wrap .content-wrap .video-wrapper .video .icon-16 {
  font-size: 64px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -32px 0 0 -32px;
  color: #fff;
}
.item .wrap .content-wrap .video-wrapper .video .minute {
  font-size: 16px;
  position: absolute;
  right: 5%;
  bottom: 5%;
  color: #fff;
}
.item .wrap .content-wrap .video-wrapper .procase {
  width: 100%;
  height: 36px;
  font-size: 14px;
  color: #fff;
  background: #3c76ff;
  text-align: center;
  line-height: 36px;
}
.item .wrap .content-wrap .video-wrapper .title {
  width: 100%;
  padding: 12px 15px 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  color: #333;
  line-height: 1.6;
  font-weight: bold;
}
.item .wrap .content-wrap .video-wrapper .number {
  display: inline-block;
  font-size: 12px;
  color: #999;
  margin-left: 13px;
}
.item .wrap .content-wrap .relative {
  margin-top: 10px;
}
.item .wrap .content-wrap .relative .list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 6px 0 10px;
  border-bottom: 1px solid #eaeaea;
}
.item .wrap .content-wrap .relative .list .left {
  position: relative;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 96px;
          flex: 0 0 96px;
  width: 96px;
}
.item .wrap .content-wrap .relative .list .left img {
  width: 100%;
  height: 82px;
}
.item .wrap .content-wrap .relative .list .left .length {
  display: inline-block;
  width: 60px;
  height: 20px;
  line-height: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: rgba(0,0,0,0.4);
  border-radius: 10px;
  position: absolute;
  padding-left: 20px;
  right: 6%;
  bottom: 15%;
  font-size: 12px;
  color: #fff;
}
.item .wrap .content-wrap .relative .list .left .icon-bofang {
  font-size: 12px;
  color: #fff;
  position: absolute;
  right: 49%;
  bottom: 21%;
}
.item .wrap .content-wrap .relative .list .right {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-left: 14px;
}
.item .wrap .content-wrap .relative .list .right .title {
  font-size: 16px;
  line-height: 1.4;
  color: #333;
}
.item .wrap .content-wrap .relative .list .right .number {
  display: inline-block;
  font-size: 12px;
  color: #999;
  margin-top: 8px;
}
.item .suggest {
  position: relative;
  margin: -64px auto 0 auto;
  height: 64px;
  line-height: 64px;
  text-align: center;
  background: #eee;
  clear: both;
  font-size: 12px;
  color: #999;
}
.item .share {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 2000;
}
.item .share .fade-enter-active,
.item .share .fade-leave-active {
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.item .share .fade-enter,
.item .share .fade-leave-active {
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}
.item .share .share-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #f1f1f1;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-bottom: 1px solid #dfdfdf;
}
.item .share .piclist {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: inline-block;
  text-align: center;
}
.item .share .piclist .gap {
  font-size: 10px;
  color: #666;
  margin-top: 8px;
  line-height: 1;
}
.item .share .piclist .pic {
  display: inline-block;
  max-width: 60px;
  background: #fff;
  border-radius: 50%;
}
@media only screen and (max-width: 320px) {
.item .share .piclist .pic {
    max-width: 40px;
}
}
.item .share .cancle {
  width: 100%;
  height: 48px;
  line-height: 48px;
  background: #f8f8f8;
  font-size: 16px;
  text-align: center;
  color: #333;
}
.item .screen {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 1;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 1000;
}


